  }
}

.contacts__tel,
.contacts__mail {
  display: flex;
  align-items: center;
}

@media (min-width: 768px) {
  .contacts__mail a,
.contacts__tel a {
    position: relative;
  }
  .contacts__mail a:hover::after,
.contacts__tel a:hover::after {
    content: "";
    position: absolute;
    top: 45px;
    left: 0;
    width: 100%;
    height: 4px;
    background-color: #9d6f3e;
  }
  .contacts__mail a:active,
.contacts__tel a:active {
    opacity: 0.6;
  }
  .contacts__mail a:active::after,
.contacts__tel a:active::after {
    opacity: 0;
  }
}

@media (min-width: 768px) {
  .contacts__mail {
    order: -1;
  }
}

.page-header__nav-toggle {
  position: relative;
  margin-top: 17px;
  margin-bottom: 17px;
  width: 30px;
  height: 3px;
  background-color: #73492a;
  border-radius: 5px;
}
.page-header__nav-toggle::before, .page-header__nav-toggle::after {
  content: "";
  position: absolute;
  display: block;
  width: 30px;
  height: 3px;
  background-color: #73492a;
  border-radius: 5px;
}
.page-header__nav-toggle::before {
  top: 10px;
}
.page-header__nav-toggle::after {
  top: -10px;
}
@media (min-width: 768px) {
  .page-header__nav-toggle {
    margin-top: 33px;
    margin-bottom: 32px;
  }
}
@media (min-width: 1440px) {
  .page-header__nav-toggle {
    display: none;
  }
}

.nav-toggle--opened {
  background-color: #fff;
}
.nav-toggle--opened::before {
  top: 0;
  transform: rotate(45deg);
}
.nav-toggle--opened::after {
  top: 0;
  transform: rotate(-45deg);
}
.nav-toggle--nojs {
  display: none;
}

.page-header__nav {
  position: fixed;
  top: 81px;
  right: 0;
  z-index: 1;
}
@media (min-width: 1440px) {
  .page-header__nav {
    flex-grow: 1;
    order: -1;
    position: static;
    min-width: 596px;
    display: flex;
    align-items: center;
    justify-content: space-around;
  }
}

@media (min-width: 1440px) {
  .header-logo--sview {
    display: none;
  }
}

.header-logo--lview {
  display: none;
}
@media (min-width: 1440px) {
  .header-logo--lview {
    display: block;
  }
}

@media (max-width: 1439px) {
  .nav--opened {
    display: block;
