  }
}

@media (max-width: 1439px) {
  .nav--closed {
    display: none;
  }
}

.nav--nojs {
  display: block;
  width: 100%;
  min-height: 433px;
  padding: 40px 25%;
  font-size: 18px;
  line-height: 1.22;
  font-weight: 700;
